import React from "react"
import { connect } from "react-redux"
import Well from "../../components/budget/Well"
import Input from "../../components/budget/Input"
import Table from "../../components/budget/Table"
import Chart from "../../components/budget/Chart"

import {
  getArray,
  ACCOUNTS_HEADINGS,
  TRANSACTIONS_ID,
  ANALYSIS_ID,
  TRANSACTIONS_HEADINGS,
  ANALYSIS_HEADINGS,
  TRANSACTIONS_OPTIONS_2,
  ANALYSIS_OPTIONS_2,
} from "../../models/budget/tables"
import { budgetTypes } from "../../types/budget/types"

import * as Tools from "../../models/budget/Tools"

const Budget = ({ portfolio, transactions, analysis }) => {
  let accounts = Tools.getAccounts(portfolio, ACCOUNTS_HEADINGS)
  accounts = ["Click to Select Account"].concat(accounts)
  let types = Tools.getTypes(transactions, TRANSACTIONS_HEADINGS)
  types = ["Select Types in Group"].concat(types)

  const TRANSACTIONS_INPUTS = (i, ii, iii, iiii) => [
    <Input
      id={TRANSACTIONS_ID}
      index={i}
      type={"select"}
      name={TRANSACTIONS_HEADINGS[0]}
      placeholder={accounts}
    />,
    <Input
      id={TRANSACTIONS_ID}
      index={ii}
      type={"number"}
      name={TRANSACTIONS_HEADINGS[1]}
      placeholder={["Debit/Credit"]}
    />,
    <Input
      id={TRANSACTIONS_ID}
      index={iii}
      type={"text"}
      name={TRANSACTIONS_HEADINGS[2]}
      placeholder={TRANSACTIONS_OPTIONS_2}
    />,
    <Input
      id={TRANSACTIONS_ID}
      index={iiii}
      type={"date"}
      name={TRANSACTIONS_HEADINGS[3]}
    />,
  ]

  const TRANSACTIONS_ARRAY = getArray(
    TRANSACTIONS_ID,
    TRANSACTIONS_INPUTS,
    transactions.length
  )

  const ANALYSIS_INPUTS = (i, ii, iii, iiii) => [
    <Input
      id={ANALYSIS_ID}
      index={i}
      type={"date"}
      name={ANALYSIS_HEADINGS[0]}
    />,
    <Input
      id={ANALYSIS_ID}
      index={ii}
      type={"date"}
      name={ANALYSIS_HEADINGS[1]}
    />,
    <Input
      id={ANALYSIS_ID}
      index={iii}
      type={"text"}
      name={ANALYSIS_HEADINGS[2]}
      placeholder={ANALYSIS_OPTIONS_2}
    />,
    <Input
      id={ANALYSIS_ID}
      index={iiii}
      type={"select"}
      name={ANALYSIS_HEADINGS[3]}
      placeholder={types}
      multipleSelection={true}
    />,
  ]

  const ANALYSIS_ARRAY = getArray(ANALYSIS_ID, ANALYSIS_INPUTS, analysis.length)

  return (
    <>
      <Well>
        <Chart type="expenses" index={1} details="Cumulative Expenses" />
        <h5>Transactions:</h5> {/* implement [Monthly/Annually]  */}
        <Table headings={TRANSACTIONS_HEADINGS} inputs={TRANSACTIONS_ARRAY} />
      </Well>
      <Well>
        <Chart type="groupings" index={2} details="Categories of Expenses" />
        <h5>Analysis:</h5> {/* implement [Monthly/Annually]  */}
        <Table headings={ANALYSIS_HEADINGS} inputs={ANALYSIS_ARRAY} />
      </Well>
    </>
  )
}

Budget.propTypes = budgetTypes

const mapStateToProps = ({ portfolio, transactions, analysis }) => {
  return { portfolio, transactions, analysis }
}
// const mapDispatchToProps = dispatch => {
//   return { clear: text => dispatch(resizeSession(text)) }
// }

// const ConnectedBudget = connect(mapStateToProps, mapDispatchToProps)(Budget)
const ConnectedBudget = connect(mapStateToProps, null)(Budget)

export default ConnectedBudget

